code[class*=language-],pre[class*=language-] {
	color: #f8f8f2;
	background: 0 0;
	text-shadow: 0 1px rgba(0,0,0,.3);
	font-family: Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
	font-size: 1em;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;
	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;
	-webkit-hyphens: none;
	-moz-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

pre[class*=language-] {
	padding: 1em;
	/* margin: .5em 0; */
	overflow: auto;
	border-radius: .3em;
}

:not(pre)>code[class*=language-],pre[class*=language-] {
	/* background: #272822; */
}

:not(pre)>code[class*=language-] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
	color: #8292a2;
}

.token.punctuation {
	color: #f8f8f2;
}

.token.namespace {
	opacity: .7;
}

.token.constant,
.token.deleted,
.token.property,
.token.symbol,
.token.tag {
	color: #f92672;
}

.token.boolean,
.token.number {
	color: #ae81ff;
}

.token.attr-name,
.token.builtin,
.token.char,
.token.inserted,
.token.selector,
.token.string {
	color: #a6e22e;
}

.language-css .token.string,
.style .token.string,
.token.entity,
.token.operator,
.token.url,
.token.variable {
	color: #f8f8f2;
}

.token.atrule,
.token.attr-value,
.token.class-name,
.token.function {
	color: #73d4ff;
}

.token.keyword {
	color: #66d9ef;
	color: #e466ef;
}

.token.important,
.token.regex {
	color: #fd971f;
}

.token.bold,
.token.important {
	font-weight: 700;
}

.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.command-line-prompt>span:before {
	color: #999;
	content: ' ';
	display: block;
	padding-right: .8em;
}

.command-line-prompt>span[data-user]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt>span[data-user=root]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt>span[data-prompt]:before {
	content: attr(data-prompt);
}
