/* Only things that don't start with `kg-` */

.kg-image-card img {
    display: block;
}

.kg-card.kg-card-hascaption figcaption{
    display: block;
    text-align: center;
    font-size: 80%;
    opacity: 0.6;
    margin-top: 0.4em;
    transition: opacity 0.3s ease;
}

.kg-card.kg-card-hascaption:hover figcaption{
    opacity: 1;
}

.kg-blockquote-alt {
    font-style: normal;
}

.gh-content .kg-callout-card .kg-callout-text,
.gh-content .kg-toggle-card .kg-toggle-content > ol,
.gh-content .kg-toggle-card .kg-toggle-content > ul,
.gh-content .kg-toggle-card .kg-toggle-content > p {
    /* font-size: 1.7rem; */
}


/* Gallery Cards
/* ---------------------------------------------------------- */

.kg-image-card:not(.kg-card-hascaption) + .kg-gallery-card,
.kg-gallery-card:not(.kg-card-hascaption) + .kg-image-card,
.kg-gallery-card:not(.kg-card-hascaption) + .kg-gallery-card {
    margin-top: var(--gallery-gap);
}

.kg-image-card.kg-card-hascaption + .kg-gallery-card,
.kg-gallery-card.kg-card-hascaption + .kg-image-card,
.kg-gallery-card.kg-card-hascaption + .kg-gallery-card {
    margin-top: calc(var(--gallery-gap) + 2vmin);
}

.kg-gallery-container {
    position: relative;
}

.kg-gallery-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.kg-gallery-image img {
    display: block;
    margin: 0;
    width: 100%;
    height: 100%;
}

.kg-gallery-row:not(:first-of-type) {
    margin: var(--gallery-gap) 0 0 0;
}

.kg-gallery-image:not(:first-of-type) {
    margin: 0 0 0 var(--gallery-gap);
}


/* Embed cards Cards
/* ---------------------------------------------------------- */

.kg-embed-card .fitVids-wrapper {
    position: relative;
    padding-bottom: 56.25%;
    width: 100%;
    height: 0;
}

.kg-embed-card .fitVids-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


/* Bookmark Cards
/* ---------------------------------------------------------- */

.kg-bookmark-card,
.kg-bookmark-publisher {
    position: relative;
}

.kg-bookmark-container,
.kg-bookmark-container:hover {
    display: flex;
    flex-direction: row-reverse;
    color: currentColor;
    background: var(--color-bookmark-bg);
    font-family: var(--font-sans-serif);
    text-decoration: none;
    border-radius: 5px;
    box-shadow: 0 2px 6px -2px rgba(0, 0, 0, 0.1), 0 0 1px rgba(0, 0, 0, 0.4);
    overflow: hidden;
}

.kg-bookmark-content {
    padding: 20px;
    order: 1;
    width: 75%;
    word-break: break-word;
}

.kg-bookmark-title {
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 1.3em;
}

.kg-bookmark-description {
    display: -webkit-box;
    max-height: 45px;
    margin: 0.5em 0 0 0;
    font-size: 1.4rem;
    line-height: 1.55em;
    overflow: hidden;
    opacity: 0.8;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.kg-bookmark-metadata {
    margin-top: 20px;
}

.kg-bookmark-metadata {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 1.3rem;
    line-height: 1.3em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.kg-bookmark-description {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}

.kg-bookmark-icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 10px;
}

.kg-bookmark-thumbnail {
    order: 2;
    width: 25%;
    display: flex;
    min-width: 120px;
}

.kg-bookmark-thumbnail img {
    max-width: 100%;
    height: auto;
    vertical-align: bottom;
    object-fit: cover;
}

.kg-bookmark-author {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.kg-bookmark-publisher::before {
    content: "â€¢";
    margin: 0 .5em;
}

.kg-bookmark-card + .kg-bookmark-card {
    margin-top: 1em;
}
